/**
 * Phoenix.Router
 */
(() => {
  "use strict";

  class PhoenixRouter extends PhoenixPlugin {
    static get is() { return 'Router' }

    static get proxies() {
      return {
        addRoute: 'add',
        route: 'route'
      };
    }

    constructor() {
      super();

      this.routes = {};
    }

    /**
     * Add a route.
     *
     * @param route
     * @param url
     *
     * @returns {PhoenixRouter}
     */
    add(route, url) {
      this.routes[route] = url;

      return this;
    }

    /**
     * Get route.
     *
     * @param route
     *
     * @returns {String}
     */
    route(route) {
      if (this.routes[route] === undefined) {
        throw new Error('Route: "' + route + '" not found');
      }

      return this.routes[route];
    }
  }

  window.PhoenixRouter = PhoenixRouter;
})();
